<template>
  <div >
    <cms-breadcrumb to="/qualifications/list" type='list' >
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt >
        <li>带 * 为必填项</li>
        <li>仅限每个用户只能保存一项增票资质</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="会员" prop="member.username" :error='errorMessage.username'>
          <el-input v-model="formData.member.username" :disabled="true" :autofocus="true" ref="first">
            <!-- <el-button @click="openDialog" slot="append" icon="el-icon-search"></el-button> -->
          </el-input>
        </el-form-item>
        <el-form-item label="公司名称" prop="companyName" :error='errorMessage.companyName'>
          <el-input v-model="formData.companyName" ></el-input>
        </el-form-item>
        <el-form-item label="税号" prop="taxNumber" :error='errorMessage.taxNumber'>
          <el-input v-model="formData.taxNumber" ></el-input>
        </el-form-item>
        <el-form-item label="开户银行名称" prop="bankName" :error='errorMessage.bankName'>
          <el-input v-model="formData.bankName" ></el-input>
        </el-form-item>
        <el-form-item label="开户银行账号" prop="bankAccountNum" :error='errorMessage.bankAccountNum'>
          <el-input v-model="formData.bankAccountNum"  ></el-input>
        </el-form-item>
        <el-form-item label="注册地址" prop="address" :error='errorMessage.address'>
          <el-input v-model="formData.address" ></el-input>
        </el-form-item>
        <el-form-item label="注册电话" prop="phoneNumber" :error='errorMessage.phoneNumber'>
          <el-input v-model="formData.phoneNumber" ></el-input>
        </el-form-item>
        <el-form-item label="审核" prop="status" :error='errorMessage.status'>
          <el-radio-group v-model="formData.status">
            <el-radio :label="1">待审核</el-radio>
            <el-radio :label="2">审核通过</el-radio>
            <el-radio :label="3">审核不通过</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="备注" prop="detail" >
          <el-input maxlength="255" v-model="formData.detail" type="textarea" :rows="3" ></el-input>
        </el-form-item>
      </el-form>
      <!-- <cms-member :member="tableData" :member-dialog="memberDialog" @headCallBack="headCall" @rowCallBack="rowCall"></cms-member> -->
      <div class="cms-form-footer">
        <cms-button @click="update" v-color >保存</cms-button>
        <!-- <cms-button type='rest' @click="rest">重置</cms-button> -->
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import list from '@/mixins/list'
import { qualifiedGet, qualifiedUpdate } from '@/api/users/qualifications.js'
// import cmsMember from '@/components/cms/cmsMember.vue'
var defaultData = {
  companyName: '',
  taxNumber: '',
  bankName: '',
  bankAccountNum: '',
  address: '',
  phoneNumber: '',
  memberId: '',
  status: 1,
  member: {
    username: ''
  }
}
export default {
  mixins: [form, list],
  data() {
    var validatePhone = (rule, value, callback) => {
      var patrnPhone = /^[0][0-9]{2,3}-[0-9]{5,10}$/
      var patrnMobile = /^1(3|4|5|7|8)\d{9}$/
      if (value === '') {
        callback(new Error('请输入注册电话'))
      } else if ((!patrnPhone.test(value)) && (!patrnMobile.test(value))) {
        callback(new Error('格式不正确'))
      } else {
        callback()
      }
    }
    return {
      memberDialog: false,
      formData: Object.assign({}, defaultData),
      rules: {
        'member.username': [rules.required('此项必填')],
        companyName: [rules.required('此项必填'), rules.len(1, 30)],
        taxNumber: [rules.required('此项必填'), rules.len(1, 30)],
        bankName: [rules.required('此项必填'), rules.len(1, 30)],
        bankAccountNum: [rules.required('此项必填'), rules.len(1, 30)],
        address: [rules.required('此项必填'), rules.len(1, 30)],
        phoneNumber: [rules.required('此项必填'), rules.custom(validatePhone)],
        status: [rules.required('此项必填')],
        detail: [rules.max(255)]
      }
    }
  },
  methods: {
    update() {
      const queryDate = {
        'id': this.formData.id,
        'companyName': this.formData.companyName,
        'taxNumber': this.formData.taxNumber,
        'bankName': this.formData.bankName,
        'bankAccountNum': this.formData.bankAccountNum,
        'address': this.formData.address,
        'phoneNumber': this.formData.phoneNumber,
        'status': this.formData.status,
        'memberId': this.formData.memberId,
        'detail': this.formData.detail
      }

      this.updateForm(qualifiedUpdate, queryDate, '/qualifications/list', 'update')
      // this.updateForm(qualifiedUpdate, this.formData, '/qualifications/list', 'update')
    },
    getFormData(id) {
      qualifiedGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    }
  },
  created() {
    this.getFormData(this.id)
  }
}
</script>

<style>
</style>



